import React from 'react';
import { useAsyncFn } from 'react-use';

import { __t } from '@mssgme/shared';
import { LandingsPropTypes } from '@mssgme/ui';
import { useFrontAPI, useSavedOrders } from '../../../../hooks';
import { printPrice } from '../../../printPrice';
import { Row } from '../../../Row';
import { OrderItemsList } from '../../../OrderItem';
import { StepsFooter } from './StepsFooter';

AdjustingStep.propTypes = {
    order: LandingsPropTypes.order.isRequired,
};

export function AdjustingStep({ order }) {
    const api = useFrontAPI();
    const orderId = order._id;
    const { removeOrderId } = useSavedOrders();

    const [{ loading }, handleModifyItems] = useAsyncFn(
        /** @param {{ productId: string, quantity: number }[]} items Order items */
        async (items) => {
            const order = await api.orders.modify({
                orderId,
                payload: {
                    items,
                },
            });

            if (!order.items.length) {
                // todo: in the future, change the logic:
                //  after last order item count brought to 0 - disable 'Next' button, but do not remove the order yet;
                //  if the modal is closed, but order items count still 0 - delete the order entirely
                removeOrderId(order._id);
            }
        },
        [orderId]
    );

    return (
        <div>
            <OrderItemsList order={order} onAdjust={order.finalized ? undefined : handleModifyItems} />
            <Row label={__t('total_to_pay')} value={printPrice(order.total)} />

            <StepsFooter next disabled={loading} />
        </div>
    );
}
